<template>
<div>
<div class="mt-36">
    <div class="text-piggy bg-auto bg-no-repeat bg-center w-auto h-auto bg-piggy pt-48 pb-48">
        <h1 class="text-center realtive z-0 mt-20 text-3xl">Savings:
            <section id="balance" class="text-pig-yellow font-semibold">£{{balance}}</section>
        </h1>
    </div>
</div>
