<script>
import axios from 'axios';

export default {
  data(){
    return { 
      balance: 0,
    }
  },
  methods:{
      loadBalance(){
          axios.get("http://localhost:8080/wallets").then(resp => {this.balance = resp.data.walletList[0].balance;});
          return this.balance;
      },
      addAnim(){

      }
  },
  created(){
      this.loadBalance();
      setInterval(() => this.loadBalance(), 500);
  },
  beforeDestroy(){
      clearInterval(this.loadBalance())
  },
      watch: {
          'balance': function (newVal, oldVal){
                var element = document.getElementById("balance");
                element.classList.add("animate-bounce");
                setTimeout(() => element.classList.remove("animate-bounce"), 550);
          }
      }
}
</script>

